import { createRouter, createWebHashHistory } from 'vue-router'

const routes = [
  {
    path: '/',
    name: 'scoutdoge',
    component: () => import('@/views/scoutdoge/index.vue'),
    meta: {
      title: 'Scout Doge'
    }
  },
  {
    path: '/redirect',
    name: 'redirect',
    component: () => import('@/views/redirect.vue'),
    meta: {
      title: 'Scout Doge'
    }
  },
  {
    path: '/dapp',
    name: 'dapp',
    component: () => import('@/views/main.vue'),
    children: [{
      path: '/dapp/presaleDetail',
      name: 'presaleDetail',
      component: () => import('@/views/presale/detail/index.vue')
    }, {
      path: '/dapp',
      name: 'presaleList',
      component: () => import('@/views/presale/list/index.vue')
    }, {
      path: '/dapp/invitation',
      name: 'invitation',
      component: () => import('@/views/invitation/index.vue')
    }, {
      path: '/dapp/promote',
      name: 'promote',
      component: () => import('@/views/promote/index.vue')
    }, {
      path: '/dapp/swap',
      name: 'swap',
      component: () => import('@/views/swap/index.vue')
    }, {
      path: '/dapp/airdrop',
      name: 'airdrop',
      component: () => import('@/views/airdrop/index.vue')
    }]
  }
]

const router = createRouter({
  history: createWebHashHistory(),
  routes
})

export default router
