<template>
  <router-view />
</template>

<script>
export default {
  mounted() {
    (function(win) {
      function refreshRem() {
        const designSize = 1920 // 设计图尺寸
        const html = document.documentElement
        const wW = html.clientWidth // 窗口宽度
        let rem = (wW * 100) / designSize
        if (rem <= 70) {
          rem = 70
        } else if (rem >= 90) {
          rem = 90
        }
        document.documentElement.style.fontSize = rem + 'px'
      }

      win.addEventListener(
        'resize',
        function() {
          refreshRem()
        },
        false
      )
      win.addEventListener(
        'pageshow',
        function(e) {
          refreshRem()
        },
        false
      )

      refreshRem()
    })(window)
  }
}
</script>

