
import { BigNumber, providers, Contract, ethers } from 'ethers'
const { ethereum } = window
import { ElNotification } from 'element-plus'
import preSaleABI from '@/api/abis/preSaleABI.json'
import scoutDogeABI from '@/api/abis/ScoutDogeABI.json'
import airDropABI from '@/api/abis/airDropABI.json'
import BLKTokenABI from '@/api/abis/IERC20.json'
import config from './config.json'
import Events from './event'
class Wallet extends Events {
  constructor(app) {
    super()
    this.hasEthereum = typeof ethereum !== 'undefined'
    this.accounts = []
    // 当前连接状态
    this.connectState = false
    // 当前合约连接状态
    this.contractState = false
    // 预售ABI
    this.preSaleABI = null
    // 邀请ABI
    this.scoutDogeABI = null
    // 空投ABI
    this.airDropABI = null
    this.init()
  }

  init() {
    if (!ethereum) {
      ElNotification({
        title: 'Tips',
        message: `Please install the wallet first                `,
        type: 'error'
      })
      return
    }
    ethereum.on('connect', (connectInfo) => {
      const timer = setInterval(() => {
        if (ethereum.isConnected()) {
          clearInterval(timer)
        }
        if (BigNumber.from(ethereum.chainId || 0)._hex === config[config.evn].chain.chainId && ethereum.selectedAddress) {
          this.initNetwork()
        }
      }, 1000)
    })

    // 初始化只读网络
    this.initOnlyReadNetwOrk()

    // 监听链路变动
    ethereum.on('chainChanged', (_chainId) => {
      // 如果当前链路跟当前环境链路不一致，则退出，并提示用户
      if (config[config.evn].chain.chainId !== _chainId) {
        ElNotification({
          title: 'Tips',
          message: `Current wallet does not support, please switch to ${config[config.evn].chain.chainName}`,
          type: 'error'
        })
        this.logout()
      } else {
        this.initNetwork()
      }
    })
  }

  // 退出钱包
  logout() {
    this.connectState = false
    this.contractState = false
    this.accountsChanged([])
  }

  // 链路检测
  async wallet_switchEthereumChain() {
    try {
      if (BigNumber.from(ethereum.chainId)._hex === config[config.evn].chain.chainId) return true
      await ethereum.request({
        method: 'wallet_switchEthereumChain',
        params: [{ chainId: config[config.evn].chain.chainId }]
      })
      return true
    } catch (error) {
      ElNotification({
        title: 'Error',
        message: error.data ? error.data.message || error.message : error.message,
        type: 'error'
      })
      return false
    }
  }

  /**
   * 连接钱包
   * @returns {List} 钱包账号列表
   */
  async connect() {
    if (!this.checkEthereum()) {
      return
    }
    const bool = await this.wallet_switchEthereumChain()
    if (!bool) {
      return
    }
    const accounts = await ethereum.request({ method: 'eth_requestAccounts' }).catch(error => {
      ElNotification({
        title: 'Error',
        message: error.data ? error.data.message || error.message : error.message,
        type: 'error'
      })
    })
    this.accountsChanged(accounts)
    // 监听账号变动
    ethereum.on('accountsChanged', (data) => {
      this.accountsChanged(data)
    })

    this.connectState = true
    return accounts
  }

  /**
   * 检测是否有metaMask
   */
  checkEthereum() {
    if (!this.hasEthereum) {
      ElNotification({
        title: 'Tips',
        message: 'Please install the meta mask plug-in first',
        type: 'error'
      })
      return false
    }
    return true
  }

  /**
   * 初始化合约
   * @param {String} address 网络地址
   * @param {JSON} abi 接口配置
   * @returns 网络请求
   */
  async initNetwork() {
    if (!this.checkEthereum()) {
      return
    }
    try {
      await this.connect()
    } catch (error) {
      ElNotification({
        title: 'Error',
        message: error.data ? error.data.message || error.message : error.message,
        type: 'error'
      })
    }
    // 当前合约状态为false，才需要初始化合约
    if (!this.contractState) {
      const provider = new providers.Web3Provider(ethereum).getSigner()
      this.preSaleABI = new Contract(config[config.evn].preSaleAddr, preSaleABI, provider)
      this.scoutDogeABI = new Contract(config[config.evn].scoutDogAddr, scoutDogeABI, provider)
      this.airDropABI = new Contract(config[config.evn].airdropAddr, airDropABI, provider)
      this.ContractBLKToken = new Contract(config[config.evn].BLKToken, BLKTokenABI, provider)
      this.contractState = true
    }
    return this
  }

  // 只读合约
  initOnlyReadNetwOrk() {
    const onlyReadProvider = new ethers.providers.JsonRpcProvider(config[config.evn].RPC)
    const onlyReadPancake = new providers.JsonRpcProvider(config[config.evn].pancakeRPC)
    const getAmountsInAbi = ['function getAmountsOut(uint amountOut, address[] calldata path) external view returns (uint[] memory amounts)']
    this.preSaleABI = new Contract(config[config.evn].preSaleAddr, preSaleABI, onlyReadProvider)
    this.scoutDogeABI = new Contract(config[config.evn].scoutDogAddr, scoutDogeABI, onlyReadProvider)
    this.pancakeRouter = new Contract(config[config.evn].pancakeAddr, getAmountsInAbi, onlyReadPancake)
  }

  /**
   * 账号变化
   */
  accountsChanged(list) {
    if (list[0] !== this.accounts[0]) {
      this.accounts = list
      this.trigger('accountChange', list)
    }
  }
}

export default {
  install: (app, options) => {
    app.config.globalProperties.$wallet = new Wallet(app)
    window.wallet = app.config.globalProperties.$wallet
  }
}

